@import "variables.less";
@import "mixins.less";

// Global Components

body {
    overflow-x: hidden;
    .ubuntu-font;
    font-weight: 300;
}

.text-muted {
    color: @gray-light;
}

.text-primary {
    color: @theme-primary;
}

p {
    font-size: 14px;
    line-height: 1.75;
}

p.large {
    font-size: 16px;
}

a,
a:hover,
a:focus,
a:active,
a.active {
    outline: none;
}

a {
    color: @theme-primary;
}

a:hover,
a:focus,
a:active,
a.active {
    color: darken(@theme-primary, 10%);
}

h1, h2, h3, h4, h5, h6 {
    .ubuntu-heading-font;
}

.img-centered {
    margin: 0 auto;
}

.bg-light-gray {
    background-color: @gray-lighter;
}

.bg-darkest-gray {
    background-color: @gray-darker;
}

// Restyled Primary Buttons

.btn-primary {
    .gradient;
    .ubuntu-heading-font;
}

.btn-xl {
    .gradient;
    .ubuntu-heading-font;
    border-radius: 3px;
    font-size: 18px;
    padding: 20px 40px;
}
.btn-primary:hover,
.btn-xl:hover {
	.gradient-flip;
	color:#fff !important; }
.btn-primary:focus,
.btn-xl:focus {
	background-color:#2d572a;
	color:#e5e5e5;
}

// Custom Navigation Bar
.navbar-custom {
    background-color: @gray-lighter;
    border-color: transparent;
    .navbar-brand {
        padding: 0;
    }
    .navbar-collapse {
        border-color: fade(white, 2%);
    }
    .navbar-toggle {
        background-color: @theme-primary;
        border-color: @theme-primary;
        .ubuntu-heading-font;
        color: white;
        font-size: 12px;
        &:hover,
        &:focus {
            background-color: @theme-primary;
        }
    }
    .nav {
        li {
            a {
                background-color:none;
                color: @theme-primary;
                &:link,
				&:visited {
					background-color:none;
					color: @theme-primary;
				}
				&:hover,
				&:active {
                    background-color:none;
                    color: @theme-primary;
                }
            }
        }
    }
}

navbar-custom.affix {
    .navbar-brand {
        padding: 4px 4px 4px 0;
    }
    -webkit-box-shadow: 0 8px 6px -6px #999;
    -moz-box-shadow: 0 8px 6px -6px #999;
    box-shadow: 0 8px 6px -6px #999;
    .nav {
        li {
            a {
                background-color:none;
                color: @theme-primary;
                &:link,
				&:visited {
					background-color:none;
					color: @theme-primary;
				}
				&:hover,
				&:active {
                    background-color:none;
                    color: @theme-primary;
                }
            }
        }
    }
}

@media(min-width:768px) {
    .navbar-custom {
        background-color: transparent;
        padding: 25px 0;
        -webkit-transition: padding 0.3s;
        -moz-transition: padding 0.3s;
        transition: padding 0.3s;
        border: none;
        .navbar-brand {
            padding: 0;
            -webkit-transition: all 0.3s;
            -moz-transition: all 0.3s;
            transition: all 0.3s;
        }
        .nav {
	        li {
	            a {
	                background-color:none;
	                color: #fff;
	                &:link,
					&:visited {
						background-color:none;
						color: #fff;
					}
					&:hover,
					&:active {
	                    background-color:@gray-lighter;
	                    color: @theme-primary;
	                }
	            }
	        }
	    }
    }
}

// Navbar Change on Scroll
@media(min-width:768px) {
    .navbar-custom.affix {
        background-color: @gray-lighter;
        padding: 10px 0;
        .navbar-brand {
            padding: 4px 4px 4px 0;
        }
        .nav {
	        li {
	            a {
	                background-color:none;
	                color: @theme-primary;
	                &:link,
					&:visited {
						background-color:none;
						color: @theme-primary;
					}
					&:hover,
					&:active {
	                    background-color:none;
	                    color: @theme-primary;
	                }
	            }
	        }
	    }
    }
}

