::-moz-selection {
    text-shadow: none;
    background: #afc39d;
}

::selection {
    text-shadow: none;
    background: #afc39d;
}

img::selection {
    background: transparent;
}

img::-moz-selection {
    background: transparent;
}

body {
    webkit-tap-highlight-color: #afc39d;
}

.features,
.features li {
	list-style: none;
	text-indent: none;
	text-align: left;
	font-weight: 300;
}